export async function getSecret(data) {
        let request = new XMLHttpRequest();
        request.open("POST", "/getSecret", true);
        request.setRequestHeader("Content-Type", "application/json");
        request.timeout = 10000;
        request.ontimeout = () => {
            request.abort();
            return null;
        }
        request.onreadystatechange = () => {
            if (request.readyState === 4 && request.status === 200) {
                return JSON.parse(request.responseText).secret;
            }
        }
        request.send(JSON.stringify(data));
    }

// Server must return json object with boolean success property
export async function makePaymentRequest(token, data) {
    let request = new XMLHttpRequest();
    request.open("POST", "/paymentRequest", true);
    request.setRequestHeader("Content-Type", "application/json");
    request.timeout = 10000;
    request.ontimeout = () => {
        request.abort();
        return null;
    }
    request.onreadystatechange = () => {
        if (request.readyState === 4 && request.status === 200) {
            return JSON.parse(request.responseText).success === "true";
        }
    }
    request.send(JSON.stringify({token: token, ...data}));
}
